<template>
  <div id="context">
    <canvas class="background"></canvas>
  </div>
</template>

<script>
import Swal from "sweetalert2";
import Loader from "@/views/loader/Loader";
import Connection from "@/rsocket/connection";
import Vue from "vue";
import router from "@/views/router/routes";

function openLoginModal() {
  Swal.fire({
    customClass: 'loginForm',
    html: `<input type="text" id="login" class="swal2-input" placeholder="Логин">
        <input type="password" id="password" class="swal2-input" placeholder="Пароль">
        <div id="recaptcha" class="g-recaptcha" data-sitekey="6LcOUV0aAAAAAFBuIViWVS4gNNoirgzEHwx64Uf4"></div>`,
    confirmButtonText: 'ВОЙТИ',
    allowOutsideClick: false,
    allowEscapeKey: false,
    focusConfirm: false,
    showCancelButton: true,
    cancelButtonText: 'ОТМЕНА',
    preConfirm: () => {
      const login = Swal.getPopup().querySelector('#login').value;
      const password = Swal.getPopup().querySelector('#password').value;
      const recaptcha = __PRODUCTION__ ? grecaptcha.getResponse() : "";
      if (!login || !password) {
        Swal.showValidationMessage(`Пожалуйста, введите логин и пароль`);
        return;
      } else if (__PRODUCTION__ && recaptcha.length === 0) {
        Swal.showValidationMessage(`Подтвердите, что вы не робот`);
        return;
      }
      Swal.showValidationMessage(`Пожалуйста подождите`)
      Connection.signIn(login, password,
          (res) => {
            let data = JSON.parse(res.data.toString());
            Vue.$log.debug(data);
            localStorage.clear();
            localStorage.setItem("token", data.token);
            router.push("/dashboard");
            Swal.close({isConfirmed: true});
          }, (error) => {
            Vue.$log.error(error);
            Swal.showValidationMessage(error.prettyText);
          });
    }
  }).then((result) => {
    if (!result.isConfirmed) {
      router.push('/');
    }
  });
  if (__PRODUCTION__) {
    grecaptcha.render('recaptcha', {
      'sitekey': '6LcOUV0aAAAAAFBuIViWVS4gNNoirgzEHwx64Uf4'
    });
  }
}

export default {
  mounted() {
    Loader.showLoader();
    let token = localStorage.key("token");
    if(token) {
      router.push("/dashboard");
      return;
    }
    Loader.closeLoader();
    openLoginModal();
  }
}
</script>

<style>
#recaptcha {
  display: -webkit-inline-flex;
}

.loginForm {
  display: block;
  width: min-content;
  min-width: 300px;
  padding: 0;
}
</style>

<style scoped>
form {
  display: inline-block;
}

::after, ::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box
}

::-webkit-scrollbar {
  display: none
}

html {
  margin: 0;
  padding: 0
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  color: #fff;
  font: 700 1rem Montserrat, Arial, sans-serif
}

.background {
  width: 100%;
  height: 100%;
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  z-index: -1;
  background: linear-gradient(135deg, #212121, #212121 26%, #480010 78%, #0c0003)
}

@keyframes gradient {
  to {
    background-position: 200% center
  }
}
</style>
